.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.markdown-text {
  font-size: 14px;
  font-weight: 400; }
  @media all and (min-width: 43.75em) {
    .markdown-text {
      font-size: 16px; } }
  .markdown-text > :first-child {
    margin-top: 0; }
  .markdown-text > :last-child {
    margin-bottom: 0; }
  .markdown-text a {
    color: #009688;
    transition: color 250ms ease-in-out; }
    .markdown-text a:hover, .markdown-text a:focus, .markdown-text a:active {
      color: #EF8354; }

.MetaData {
  margin: 0;
  padding: 0;
  text-align: center; }
  .MetaData-item {
    color: rgba(0, 0, 0, 0.54);
    display: inline-block;
    list-style: none;
    margin-right: 16px;
    text-transform: capitalize; }
    .MetaData-item:last-of-type {
      margin-right: 0; }
    .MetaData-item a {
      color: rgba(0, 0, 0, 0.54);
      text-decoration: none;
      transition: color 250ms ease-in-out; }
      .MetaData-item a:hover, .MetaData-item a:focus, .MetaData-item a:active {
        color: #EF8354; }

.Pagination {
  position: relative;
  text-align: center; }
